import React from "react";
import BotonFormulas from "./BotonFormulas";

const PantallaEleccion = ({ control }) => {
  if (window.location.pathname === '/Areas'){
    return (
      <div className="contenedor-formulas">
        <ul className="container-fluid">
          <h3>Areas:</h3>
          <div className="row"><li><BotonFormulas formulaClick={control} formula='At'/>rea del tringulo: A = b(base) * h(altura) / 2</li></div>
          <div className="row"><li><BotonFormulas formulaClick={control} formula='Arec' /> rea del rectngulo: A = b(base) * h(altura)</li></div>
          <div className="row"><li><BotonFormulas formulaClick={control} formula='Ac' /> rea del cuadrado: A = l(lado)^2</li></div>
          <div className="row"><li><BotonFormulas formulaClick={control} formula='Aci' /> rea del circulo: A = *r(radio)^2</li></div>
          <div className="row"><li><BotonFormulas formulaClick={control} formula='Asc' /> rea del sector del circulo: A = (a(ngulo central)/360) *  * r(radio)^2</li></div>
        </ul>
      </div>
    )
  }
  else{
    return(
      <div>

      </div>
    );
  }
}

export default PantallaEleccion